import React, { useState, useEffect } from "react";
import { AdminPage, Panel } from "@admin/components/admin";
import Table from "@admin/components/common/Table";
import "./style.scss";
import {
  Button,
  DatePicker,
  Tooltip,
  Form,
  Col,
  Row,
  Input,
  Select,
  Icon,
  TimePicker,
  Checkbox,
  Modal,
  Upload,
} from "antd";
import commerciaNameProvider from "@data-access/commercial-name-provider";
import unitProvider from "@data-access/unit-provider";
import manufacturerProvider from "@data-access/manufacturer-provider";
import supplierProvider from "@data-access/supplier-provider";
import countryProvider from "@data-access/country-provider";
import deviceProvider from "@data-access/device-provider";
import statusProvider from "@data-access/status-provider";
import snackbar from "@utils/snackbar-utils";
const { Option } = Select;
const { confirm } = Modal;
function index(props) {
  let deviceId = props.match.params.id
  const [state, _setState] = useState({
    size: 10,
    page: 1,
    data: [],
    listNames: [],
    listUnits: [],
    listManufacturers: [],
    listSupplier: [],
    listCountry: [],
    listStatus: [],

    dmThietBiId: undefined,
    ten: "",
    soLuong: 1,
    serial: "",
    model: "",
    dmDonViTinhId: undefined,
    dmHangSanXuatId: undefined,
    dmDonViCungCapId: undefined,
    namSuDung: undefined,
    nuocSanXuatId: undefined,
    namSanXuat: undefined,
    namMua: undefined,
    giaNhap: "",
    dmtrangThaiId: undefined,
    donViSuDung: "",
    ghiChu: "",
    isTBPhuTro: false,

    dmThietBiPTId: undefined,
    dmThietBiPTName: "",
    soLuongPT: 1,
    serialPT: "",
    dmHangSanXuatPTId: undefined,
    dmHangSanXuatPTName: '',
    nuocSanXuatPTId: undefined,
    nuocSanXuatPTName: "",
    namMuaPT: undefined,
    trangThaiPT: undefined,
    trangThaiPTName: "",
    listTBPT: [],
    dataPT: [],
    isEditPT: false,
    keyEdit: undefined
  });

  const setState = (data = {}) => {
    _setState((state) => {
      return { ...state, ...data };
    });
  };
  const { getFieldDecorator } = props.form;

  useEffect(() => {
    onSearchStatus();
    onSearchName();
    onSearchUnit();
    onSearchManufacturer();
    onSearchSupplier();
    onSearchCountry()
    if (deviceId) {
      getDetail(deviceId);
    }
  }, []);
  const getDetail = (id) => {
    deviceProvider.getById(id).then(s => {
      if (s && s.code === 0) {
        let data = s.data
        setState({
          dmThietBiId: data.dmThietBiId,
          ten: data.ten,
          soLuong: data.soLuong,
          serial: data.serial,
          model: data.model,
          dmDonViTinhId: data.dmDonViTinhId,
          dmHangSanXuatId: data.dmHangSanXuatId,
          dmDonViCungCapId: data.dmDonViCungCapId,
          donViSuDung: data.donViSuDung,
          namSuDung: data.namSuDung,
          nuocSanXuatId: data.nuocSanXuatId,
          namSanXuat: data.namSanXuat,
          namMua: data.namMua,
          giaNhap: data.giaNhap,
          dmtrangThaiId: data.dmtrangThaiId,
          ghiChu: data.ghiChu,
          isTBPhuTro: data.thietBiPhuTro && data.thietBiPhuTro.length ? true : false
        })
      }
    }).catch(e => { })
  }
  const onSearchStatus = (page = 0, size = 10) => {
    statusProvider.search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listStatus: s.data,
          });
        }
      })
      .catch((e) => { });
  };
  const onSearchName = (page = 0, size = 10) => {
    commerciaNameProvider
      .search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listNames: s.data,
          });
        }
      })
      .catch((e) => { });
  };
  const onSearchUnit = (page = 0, size = 10) => {
    unitProvider
      .search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listUnits: s.data,
          });
        }
      })
      .catch((e) => { });
  };
  const onSearchManufacturer = (page = 0, size = 10) => {
    manufacturerProvider
      .search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listManufacturers: s.data,
          });
        }
      })
      .catch((e) => { });
  };
  const onSearchSupplier = (page = 0, size = 10) => {
    supplierProvider
      .search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listSupplier: s.data,
          });
        }
      })
      .catch((e) => { });
  };
  const onSearchCountry = (page = 0, size = 10) => {
    countryProvider
      .search(page, size)
      .then((s) => {
        if (s.code == 0) {
          setState({
            listCountry: s.data,
          });
        }
      })
      .catch((e) => { });
  };

  const onCheckDate = () => {
    let x = 1990;
    let check = [];
    do {
      check.push(x);
      x = x + 1;
    } while (x <= new Date().getFullYear());
    let c = check.map((item, index) => {
      return (
        <Option key={index} value={item}>
          {item}
        </Option>
      );
    });
    return c;
  };
  const onCheckUsedDate = () => {
    let x = 1990;
    let check = [];
    do {
      check.push(x);
      x = x + 1;
    } while (x <= new Date().getFullYear() + 10);
    let c = check.map((item, index) => {
      return (
        <Option key={index} value={item}>
          {item}
        </Option>
      );
    });
    return c;
  };
  const onSubmit = (e) => {
    e.preventDefault();
    let {
      dmThietBiId,
      ten,
      soLuong,
      serial,
      model,
      dmDonViTinhId,
      dmHangSanXuatId,
      dmDonViCungCapId,
      namSuDung,
      nuocSanXuatId,
      namSanXuat,
      namMua,
      giaNhap,
      dmtrangThaiId,
      ghiChu,
      donViSuDung,
      listTBPT
    } = state;
    let TBPhuTro = listTBPT && listTBPT.length ? listTBPT.map(item => {
      return {
        dmThietBiId: item.dmThietBiPTId,
        serial: item.serialPT,
        dmHangSanXuatId: item.dmHangSanXuatPTId,
        nuocSanXuatId: item.nuocSanXuatPTId,
        namMua: item.namMuaPT,
        dmTrangThaiId: item.trangThaiPT
      };
    }) : []

    let param = {
      dmThietBiId,
      ten,
      soLuong,
      serial,
      model,
      dmDonViTinhId,
      dmHangSanXuatId,
      dmDonViCungCapId,
      namSuDung,
      nuocSanXuatId,
      namSanXuat,
      namMua,
      giaNhap,
      dmtrangThaiId,
      ghiChu,
      donViSuDung,
      thietBiPhuTro: TBPhuTro
    };
    props.form.validateFields((err, values) => {
      if (!err) {
        confirm({
          title: "Xác nhận",
          content: `Bạn có muốn thêm ${soLuong} thiết bị ${ten} vào hệ thống không? `,
          okText: "Đồng ý",
          okType: "primary",
          cancelText: "Hủy",
          onOk() {
            deviceProvider
              .createOrEdit("", param)
              .then((s) => {
                if (s && s.code === 0) {
                  snackbar.show("Thêm thành công", "success");
                  props.history.push("/admin/device");
                } else if (s.code === 1000) {
                  snackbar.show("Thiếu thông tin", "danger");
                }
              })
              .catch((x) => { });
          },
          onCancel() {
          },
        });
      }
    });
  };
  const onSubmitPT = (e) => {
    e.preventDefault()
    let {
      dmThietBiPTId,
      dmThietBiPTName,
      soLuongPT,
      serialPT,
      dmHangSanXuatPTId,
      dmHangSanXuatPTName,
      nuocSanXuatPTId,
      nuocSanXuatPTName,
      namMuaPT,
      trangThaiPT,
      trangThaiPTName,
      listTBPT,
      isEditPT,
      keyEdit
    } = state
    if (!dmThietBiPTId) {
      snackbar.show("Vui lòng nhập tên thiết bị", "danger");
      return
    } else if (!soLuongPT) {
      snackbar.show("Vui lòng nhập số lượng", "danger");
      return
    } else if (!namMuaPT) {
      snackbar.show("Vui lòng chọn năm mua sắm", "danger");
      return
    }
    let param = {
      dmThietBiPTId,
      dmThietBiPTName,
      soLuongPT,
      serialPT,
      dmHangSanXuatPTId,
      dmHangSanXuatPTName,
      nuocSanXuatPTId,
      nuocSanXuatPTName,
      namMuaPT,
      trangThaiPT,
      trangThaiPTName
    }
    if (isEditPT) {
      let checkValue = listTBPT.filter((item, index) => keyEdit !== index)
      checkValue.push(param)
      setState({
        listTBPT: checkValue,
        dataPT: checkValue.map((item, index) => {
          return {
            key: index,
            col1: index,
            col2: item.dmThietBiPTName,
            col3: item.serialPT,
            col4: item.dmHangSanXuatPTName,
            col5: item.nuocSanXuatPTName,
            col6: item.namMuaPT,
            col7: item.trangThaiPTName,
            col8: index
          };
        }),
        dmThietBiPTId: undefined,
        dmThietBiPTName: "",
        soLuongPT: 1,
        serialPT: "",
        dmHangSanXuatPTId: undefined,
        dmHangSanXuatPTName: "",
        nuocSanXuatPTId: undefined,
        nuocSanXuatPTName: '',
        namMuaPT: undefined,
        trangThaiPT: undefined,
        trangThaiPTName: '',
        isEditPT: false,
        keyEdit: undefined
      })
    } else {
      setState({
        listTBPT: state.listTBPT.concat(param),
        dataPT: state.listTBPT.concat(param).map((item, index) => {
          return {
            key: index,
            col1: index,
            col2: item.dmThietBiPTName,
            col3: item.serialPT,
            col4: item.dmHangSanXuatPTName,
            col5: item.nuocSanXuatPTName,
            col6: item.namMuaPT,
            col7: item.trangThaiPTName,
            col8: index,
          };
        }),
        dmThietBiPTId: undefined,
        dmThietBiPTName: "",
        soLuongPT: 1,
        serialPT: "",
        dmHangSanXuatPTId: undefined,
        dmHangSanXuatPTName: "",
        nuocSanXuatPTId: undefined,
        nuocSanXuatPTName: "",
        namMuaPT: undefined,
        trangThaiPT: undefined,
        trangThaiPTName: "",
        isEditPT: false,
        keyEdit: undefined
      })
    }
  }
  const onDeleteItem = (key) => {
    confirm({
      title: "Xác nhận",
      content: `Bạn có muốn xóa thiết bị phụ trợ này?`,
      okText: "Xóa",
      okType: "danger",
      cancelText: "Hủy",
      onOk() {
        let checkValue = state.listTBPT.filter((item, index) => key !== index)
        setState({
          listTBPT: checkValue,
          dataPT: checkValue.map((item, index) => {
            return {
              key: index,
              col1: index,
              col2: item.dmThietBiPTName,
              col3: item.serialPT,
              col4: item.dmHangSanXuatPTName,
              col5: item.nuocSanXuatPTName,
              col6: item.namMuaPT,
              col7: item.trangThaiPTName,
              col8: index,
            }
          })
        })
      },
      onCancel() {
      },
    });

  }
  const editItemPT = (key) => {
    state.listTBPT.find((item, index) => {
      if (key === index) {
        setState({
          dmThietBiPTId: item.dmThietBiPTId,
          soLuongPT: item.soLuongPT,
          serialPT: item.serialPT,
          dmHangSanXuatPTId: item.dmHangSanXuatPTId,
          nuocSanXuatPTId: item.nuocSanXuatPTId,
          namMuaPT: item.namMuaPT,
          trangThaiPT: item.trangThaiPT,
          isEditPT: true,
          keyEdit: key
        })
      }
    })

  };
  return (
    <div>
      <AdminPage
        icon="subheader-icon fal fa-window"
        header="Thêm mới thiết bị"
        subheader="Nhập thông tin thiết bị cần thêm"
      >
        <Panel id="panel-list-site" title="THÔNG TIN THIẾT BỊ">
          <Form layout="vertical" hideRequiredMark>
            <div className="container div-combo9">
              <div className="row">
                <div className="col-md-4">
                  <Form.Item label="Tên thiết bị *" >
                    {getFieldDecorator("dmThietBiId", {
                      rules: [
                        {
                          required: true,
                          message: "Chọn tên thiết bị",
                        },
                      ],
                      initialValue: state.dmThietBiId,
                    })(
                      <Select
                        placeholder="Chọn tên thiết bị"
                        onChange={(e) => {
                          setState({
                            dmThietBiId: e,
                          });
                        }}
                        value={state.dmThietBiId}
                        showSearch
                        filterOption={(input, option) =>
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {state.listNames && state.listNames.length
                          ? state.listNames.map((item, index) => {
                            return (
                              item && (
                                <Option key={index} value={item.id}>
                                  {item.ten}
                                </Option>
                              )
                            );
                          })
                          : ""}
                      </Select>
                    )}
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item
                    name="name"
                    label="Tên thương mại"
                    rules={[{ required: true, message: "Nhập tên thương mại" }]}
                  >
                    <Input
                      placeholder="Nhập tên thương mại"
                      onChange={(e) => {
                        setState({
                          ten: e.target.value,
                        });
                      }}
                      value={state.ten}
                    />
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item
                    name="name"
                    label="Số lượng *"
                    rules={[{ required: true, message: "Nhập tên thương mại" }]}
                  >
                    {getFieldDecorator("soLuong", {
                      rules: [
                        {
                          required: true,
                          message: "Nhập số lượng",
                        },
                      ],
                      initialValue: state.soLuong,
                    })(
                      <Input
                        placeholder="Nhập số lượng"
                        type="number"
                        onChange={(e) => {
                          setState({
                            soLuong: e.target.value,
                          });
                        }}
                        value={state.soLuong}
                      />
                    )}
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <Form.Item
                    name="name"
                    label="Serial"
                    rules={[{ required: true, message: "Nhập serial" }]}
                  >
                    <Input
                      placeholder="Nhập serial"
                      onChange={(e) => {
                        setState({
                          serial: e.target.value,
                        });
                      }}
                      value={state.serial}
                    />
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item
                    name="name"
                    label="Chủng loại (model)"
                    rules={[{ required: true, message: "Nhập chủng loại" }]}
                  >
                    <Input
                      placeholder="Nhập chủng loại"
                      onChange={(e) => {
                        setState({
                          model: e.target.value,
                        });
                      }}
                      value={state.model}
                    />
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item label="Đơn vị tính *">
                    {getFieldDecorator("dmDonViTinhId", {
                      rules: [
                        {
                          required: true,
                          message: "Vui lòng nhập đơn vị tính",
                        },
                      ],
                      initialValue: state.dmDonViTinhId,
                    })(
                      <Select
                        placeholder="Chọn đơn vị tính"
                        onChange={(e) => {
                          setState({
                            dmDonViTinhId: e,
                          });
                        }}
                        value={state.dmDonViTinhId}
                        showSearch
                        filterOption={(input, option) =>
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {state.listUnits && state.listUnits.length
                          ? state.listUnits.map((item, index) => {
                            return (
                              item && (
                                <Option key={index} value={item.id}>
                                  {item.ten}
                                </Option>
                              )
                            );
                          })
                          : ""}
                      </Select>
                    )}
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <Form.Item label="Hãng sản xuất">
                    <Select
                      placeholder="Chọn hãng sản xuất"
                      onChange={(e) => {
                        setState({
                          dmHangSanXuatId: e,
                        });
                      }}
                      value={state.dmHangSanXuatId}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listManufacturers && state.listManufacturers.length
                        ? state.listManufacturers.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item label="Nước sản xuất">
                    <Select
                      placeholder="Nước sản xuất"
                      onChange={(e) => {
                        setState({
                          nuocSanXuatId: e,
                        });
                      }}
                      value={state.nuocSanXuatId}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listCountry && state.listCountry.length
                        ? state.listCountry.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item label="Năm sản xuất">
                    <Select
                      placeholder="Chọn năm sản xuất"
                      onChange={(e) => {
                        setState({
                          namSanXuat: e,
                        });
                      }}
                      value={state.namSanXuat}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {onCheckDate()}
                    </Select>
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <Form.Item label="Năm sử dụng">
                    <Select
                      placeholder="Chọn năm sử dụng"
                      onChange={(e) => {
                        setState({
                          namSuDung: e,
                        });
                      }}
                      value={state.namSuDung}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {onCheckUsedDate()}
                    </Select>
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item
                    name="name"
                    label="Nguyên giá"
                    rules={[
                      { required: true, message: "Please enter user name" },
                    ]}
                  >
                    <Input
                      placeholder="Nhập nguyên giá"
                      type="number"
                      onChange={(e) => {
                        setState({
                          giaNhap: e.target.value,
                        });
                      }}
                      value={state.giaNhap}
                    />
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item name="name" label="Đơn vị sử dụng">
                    <Input
                      placeholder="Nhập đơn vị sử dụng"
                      onChange={(e) => {
                        setState({
                          donViSuDung: e.target.value,
                        });
                      }}
                      value={state.donViSuDung}
                    />
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <Form.Item label="Năm mua sắm *">
                    {getFieldDecorator("namMua", {
                      rules: [
                        {
                          required: true,
                          message: "Vui lòng chọn năm mua sắm",
                        },
                      ],
                      initialValue: state.namMua,
                    })(
                      <Select
                        placeholder="Chọn năm mua sắm"
                        onChange={(e) => {
                          setState({
                            namMua: e,
                          });
                        }}
                        value={state.namMua}
                        showSearch
                        filterOption={(input, option) =>
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {onCheckUsedDate()}
                      </Select>
                    )}
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item label="Đơn vị cung cấp *">
                    {getFieldDecorator("dmDonViCungCapId", {
                      rules: [
                        {
                          required: true,
                          message: "Vui lòng chọn đơn vị cung cấp",
                        },
                      ],
                      initialValue: state.dmDonViCungCapId,
                    })(
                      <Select
                        placeholder="Đơn vị cung cấp"
                        onChange={(e) => {
                          setState({
                            dmDonViCungCapId: e,
                          });
                        }}
                        value={state.dmDonViCungCapId}
                        showSearch
                        filterOption={(input, option) =>
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {state.listSupplier && state.listSupplier.length
                          ? state.listSupplier.map((item, index) => {
                            return (
                              item && (
                                <Option key={index} value={item.id}>
                                  {item.ten}
                                </Option>
                              )
                            );
                          })
                          : ""}
                      </Select>
                    )}
                  </Form.Item>
                </div>
                <div className="col-md-4">
                  <Form.Item label="Trạng thái thiết bị *">
                    {getFieldDecorator("dmtrangThaiId", {
                      rules: [
                        {
                          required: true,
                          message: "Vui lòng chọn trạng thái",
                        },
                      ],
                      initialValue: state.dmtrangThaiId,
                    })(
                      <Select
                        placeholder="Chọn trạng thái thiết bị"
                        onChange={(e) => {
                          setState({
                            dmtrangThaiId: e,
                          });
                        }}
                        value={state.dmtrangThaiId}
                        showSearch
                        filterOption={(input, option) =>
                          option.props.children
                            .toLowerCase()
                            .indexOf(input.toLowerCase()) >= 0
                        }
                      >
                        {state.listStatus && state.listStatus.length
                          ? state.listStatus.map((item, index) => {
                            return (
                              item && (
                                <Option key={index} value={item.id}>
                                  {item.ten}
                                </Option>
                              )
                            );
                          })
                          : ""}
                      </Select>
                    )}
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <Form.Item
                    name="description"
                    label="Ghi chú"
                    rules={[
                      {
                        required: true,
                        message: "Nhập ghi chú",
                      },
                    ]}
                  >
                    <Input.TextArea
                      rows={5.5}
                      placeholder="Nhập ghi chú"
                      onChange={(e) => {
                        setState({
                          ghiChu: e.target.value,
                        });
                      }}
                      value={state.ghiChu}
                    />
                  </Form.Item>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12" style={{ marginBottom: 10 }}>
                  <Checkbox checked={state.isTBPhuTro} onChange={() => { setState({ isTBPhuTro: !state.isTBPhuTro }) }}>Nhập thiết bị phụ trợ</Checkbox>
                </div>
              </div>
            </div>
            {
              state.isTBPhuTro ? <div>
                <Table
                  scroll={{ x: 800, y: 500 }}
                  style={{ marginLeft: -10, marginRight: -10 }}
                  className="custom"
                  columns={[
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Mã thiết bị</div>
                        </div>
                      ),
                      width: 200,
                      dataIndex: "col1",
                      key: "col1",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Tên thiết bị</div>
                        </div>
                      ),
                      width: 300,
                      dataIndex: "col2",
                      key: "col2",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Serial</div>
                        </div>
                      ),
                      width: 300,
                      dataIndex: "col3",
                      key: "col3",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Hãng sản xuất</div>
                        </div>
                      ),
                      width: 300,
                      dataIndex: "col4",
                      key: "col4",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Nước sản xuất</div>
                        </div>
                      ),
                      width: 300,
                      dataIndex: "col5",
                      key: "col5",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Năm mua sắm</div>
                        </div>
                      ),
                      width: 200,
                      dataIndex: "col6",
                      key: "col6",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Trạng thái thiết bị</div>
                        </div>
                      ),
                      width: 300,
                      dataIndex: "col7",
                      key: "col7",
                      fixed: "right",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Tiện ích</div>
                        </div>
                      ),
                      key: "operation",
                      width: 100,
                      fixed: "right",
                      render: (item) => {
                        return (
                          <div className="col-action">
                            <Tooltip placement="topLeft" title={"Xóa"}>
                              <div>
                                <a
                                  onClick={() => onDeleteItem(item)}
                                  className="btn btn-info btn-icon waves-effect waves-themed"
                                >
                                  <i className="fal fa-trash-alt"></i>
                                </a>
                              </div>
                            </Tooltip>
                            <Tooltip placement="topLeft" title={"Sửa"}>
                              <div>
                                <a
                                  onClick={() => editItemPT(item)}
                                  className="btn btn-info btn-icon waves-effect waves-themed"
                                >
                                  <i className="fal fa-edit"></i>
                                </a>
                              </div>
                            </Tooltip>
                          </div>
                        );
                      },
                      dataIndex: "col8",
                      key: "col8",
                    },
                  ]}
                  dataSource={state.dataPT}
                ></Table>
                <div className="footer style-input10">
                  <Form.Item className="sty-selcect">
                    <Select
                      value={state.dmThietBiPTId}
                      placeholder="Chọn tên thiết bị"
                      onChange={(e, i) => {
                        setState({
                          dmThietBiPTId: e,
                          dmThietBiPTName: i.props.children
                        });
                      }}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listNames && state.listNames.length
                        ? state.listNames.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                  <Form.Item
                    name="name"
                  >
                    <Input
                      className="sty-input9"
                      placeholder="Nhập số lượng"
                      onChange={(e) => {
                        setState({
                          soLuongPT: e.target.value,
                        });
                      }}
                      value={state.soLuongPT}
                    />
                  </Form.Item>
                  <Form.Item
                    name="name"
                  >
                    <Input
                      className="sty-input9"
                      placeholder="Nhập số serial ngăn cách bởi dấu phím enter"
                      onChange={(e) => {
                        setState({
                          serialPT: e.target.value,
                        });
                      }}
                      value={state.serialPT}
                    />
                  </Form.Item>
                  <Form.Item className="sty-selcect">
                    <Select
                      value={state.dmHangSanXuatPTId}
                      placeholder="Chọn hãng sản xuất"
                      onChange={(e, i) => {
                        setState({
                          dmHangSanXuatPTId: e,
                          dmHangSanXuatPTName: i.props.children
                        });
                      }}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listManufacturers && state.listManufacturers.length
                        ? state.listManufacturers.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                  <Form.Item className="sty-selcect">
                    <Select
                      value={state.nuocSanXuatPTId}
                      placeholder="Chọn Nước sản xuất"
                      onChange={(e, i) => {
                        setState({
                          nuocSanXuatPTId: e,
                          nuocSanXuatPTName: i.props.children
                        });
                      }}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listCountry && state.listCountry.length
                        ? state.listCountry.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                  <Form.Item className="sty-selcect">
                    <Select
                      value={state.namMuaPT}
                      placeholder="Chọn năm mua sắm"
                      onChange={(e) => {
                        setState({
                          namMuaPT: e,
                        });
                      }}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {onCheckDate()}
                    </Select>
                  </Form.Item>
                  <Form.Item className="sty-selcect">
                    <Select
                      value={state.trangThaiPT}
                      placeholder="Chọn trạng thái thiết bị"
                      onChange={(e, i) => {
                        setState({
                          trangThaiPT: e,
                          trangThaiPTName: i.props.children
                        });
                      }}
                      showSearch
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {state.listStatus && state.listStatus.length
                        ? state.listStatus.map((item, index) => {
                          return (
                            item && (
                              <Option key={index} value={item.id}>
                                {item.ten}
                              </Option>
                            )
                          );
                        })
                        : ""}
                    </Select>
                  </Form.Item>
                  <Button
                    type="primary"
                    onClick={(e) => onSubmitPT(e)}
                  >
                    <i className="fal fa-plus-circle" style={{ color: "#fff" }}></i>
                  </Button>
                </div>
              </div> : ""
            }


            <div
              className="style-button pull-right"
              style={{ display: "flex", justifyContent: "flex-end" }}
            >
              <Button
                type="primary"
                style={{ marginRight: 8 }}
                onClick={(e) => onSubmit(e)}
              >
                Thêm mới
              </Button>
              <Button type="danger" htmlType="submit">
                Reset
              </Button>
            </div>
          </Form>
        </Panel>
      </AdminPage>
    </div>
  );
}
export default Form.create()(index);
