import client from "../utils/client-utils";
import constants from "../resources/strings";

export default {
  getById(id) {
    let url = constants.api.nameDevice.search + "/" + id;
    return client.requestApi("get", url, {});
  },

  search(page, size, name, active, selected) {
    let url = constants.api.nameDevice.search + "?";
    url += "page=" + (page || 0) + "&";
    url += "size=" + (size || 10) + "&";
    if (name) url += "name=" + name + "&";
    if (active !== undefined && active != -1)
      url += "active=" + (active ? 1 : 0) + "&";
    if (selected !== undefined) url += "selected=" + (selected ? 1 : 0) + "&";
    return client.requestApi("get", url, {});
  },
  delete(id) {
    let url = constants.api.nameDevice.search + "/" + id;
    return client.requestApi("delete", url, {});
  },
  createOrEdit(id, ma, ten, tenVietTat, dmLoaiThietBiId) {
    if (!id) {
      let url = constants.api.nameDevice.search;
      return client.requestApi("post", url, {
        ma,
        ten,
        tenVietTat,
        dmLoaiThietBiId
      });
    } else {
      let url = constants.api.nameDevice.search + "/" + id;
      return client.requestApi("put", url, {
        ma,
        ten,
        tenVietTat,
        dmLoaiThietBiId
      });
    }
  },
};
